import conversation from "./chooseCourse/img/conversation.png"
import bigBan from "./chooseCourse/img/big_ban.png"
import doMore from"./chooseCourse/img/do_more.png"
import klava from "./chooseCourse/img/klava.png"
import discussion from "./chooseCourse/img/discussion.png"
import oneMoreConversation from"./chooseCourse/img/oneMoreConversation.png"
class ChooseCourse {
    constructor() {
        this.container = document.querySelector('.course-list__cards')
        this.data = [conversation,bigBan,doMore,klava,discussion,oneMoreConversation]
    }
    render(img1, img2, img3, img4, img5, img6) {
        
        let htmlContent = ` 
                <div class="close-event__card card course-list__size-lesson" 
                data-sort-data="12" data-sort-duration="90" data-sort-popular="8" data-filter-purpose="1" 
                data-filter-price="1,2" data-filter-duration="0">
                    <div class="card__title">-</div>
                    <img src=${img1} alt="classroom" class="card__img">
                    <p class="card__description">    :  ,  ,  </p>
                    <div class="card__footer">
                        <div class="card__price course-list__price-lesson"><span>&nbsp;</span> 10 000 .&nbsp;<span> </span></div>
                        <a href="course_eng-for-travel.html" class="card__link _link-white"></a>
                    </div>
                </div>
                <div class="close-event__card card course-list__size-lesson" 
                data-sort-data="9" data-sort-duration="120" data-sort-popular="5"  data-filter-purpose="0,1"
                 data-filter-price="1,2" data-filter-duration="0">
                    <div class="card__title">  </div>
                    <img src=${img2} alt="classroom" class="card__img">
                    <p class="card__description"> !    
                        ,       </p>
                    <div class="card__footer">
                        <div class="card__price course-list__price-lesson"><span>&nbsp;</span> 8 000 .&nbsp;<span> </span></div>
                        <a href="course_eng-for-travel.html" class="card__link _link-white"></a>
                    </div>
                </div>
                <div class="close-event__card card course-list__size-lesson"
                 data-sort-data="9" data-sort-duration="70" data-sort-popular="10"  data-filter-purpose="0"
                  data-filter-price="0,1,2" data-filter-duration="0">
                    <div class="card__title"> Beginner  Elementary</div>
                    <img src=${img3} alt="classroom" class="card__img">
                    <p class="card__description">    
                               elementary.</p>
                    <div class="card__footer">
                        <div class="card__price course-list__price-lesson"><span>&nbsp;</span> 5 000 .&nbsp;<span> </span></div>
                        <a href="course_eng-for-travel.html" class="card__link _link-white"></a>
                    </div>
                </div>
                <div class="close-event__card card course-list__size-lesson"
                 data-sort-data="8" data-sort-duration="50" data-sort-popular="9" data-filter-price="2"
                  data-filter-duration="1">
                    <div class="card__title">IT-</div>
                    <img src=${img4} alt="classroom" class="card__img">
                    <p class="card__description">    , ,     IT </p>
                    <div class="card__footer">
                        <div class="card__price course-list__price-lesson"><span>&nbsp;</span> 12 000 .&nbsp;<span> </span></div>
                        <a href="course_eng-for-travel.html" class="card__link _link-white"></a>
                    </div>
                </div>
                <div class="close-event__card card course-list__size-lesson"
                 data-sort-data="11" data-sort-duration="150" data-sort-popular="5"  data-filter-purpose="0,1" data-filter-price="1,2"
                  data-filter-duration="2">
                    <div class="card__title">  </div>
                    <img src=${img5} alt="classroom" class="card__img">
                    <p class="card__description">   
                           .
                           .</p>
                    <div class="card__footer">
                        <div class="card__price course-list__price-lesson"><span>&nbsp;</span> 8 000 .&nbsp;<span> </span></div>
                        <a href="course_eng-for-travel.html" class="card__link _link-white"></a>
                    </div>
                </div>
                <div class="close-event__card card course-list__size-lesson"
                 data-sort-data="10" data-sort-duration="60" data-sort-popular="6"  data-filter-purpose="2" 
                 data-filter-price="0,1,2" data-filter-duration="1">
                    <div class="card__title">  </div>
                    <img src=${img6} alt="classroom" class="card__img">
                    <p class="card__description">   -   , 
                            .     .</p>
                    <div class="card__footer">
                        <div class="card__price course-list__price-lesson"><span>&nbsp;</span> 5 000 .&nbsp;<span> </span></div>
                        <a href="course_eng-for-travel.html" class="card__link _link-white"></a>
                    </div>
                </div>
            `;
                
        this.container.innerHTML = htmlContent;
    }
    open(img1, img2, img3, img4, img5, img6) {
        this.render(img1, img2, img3, img4, img5, img6)
        this.container.classList.add('_ul-open')
    }

    addListenerForChangePage() {
        let ulContainer = document.querySelector('.course-list__number-page')
        let children = ulContainer.children
        this.open(...this.data)
        for(let child of children) {
            child.addEventListener('click', () => {
                for(let child of children) {
                    child.classList.remove('_li-active')
                }
                child.classList.add('_li-active')
                this.data.reverse()
                this.open(...this.data)
            })
        }
    }
};

export default new ChooseCourse();